.scrollSection{
    background-color: coral;
    color: white;
    font-family:monospace;
    font-size: 2rem;
    display: flex;
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    scroll-snap-align:start;
} 
 
.scrollContainer{
    scroll-snap-type:y mandatory;
    overflow-y: scroll;
    height: 100vh;
}